.footer {
  background-color: white;
  opacity: 1;
  width: 100%;
  color: black;
  padding: 1rem;
  position: relative;
  overflow: hidden;
  line-height: 2.9rem;
  z-index: 3;
  font-size: 2rem;
  overflow: visible;

  & a {
  }



  
  &__sailfish1 {
    position: absolute;
    bottom: 0;
    z-index: -100
  }
  &__image {
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    position: absolute;
    width: 60%;
    min-width: 50rem;
    gap: 1rem;
    fill: darken($color3, 4%);
    z-index: -1;
  }

  &__copyright {
    text-align: center;
  }

  &__address {
    border-radius: 4px;
    padding: 2rem;
    width: 30rem;
    gap: 2rem;
    @include flexCenter(column);
  }

  & li {
    cursor: pointer;
    &:hover {
      transform: scale(1.01);
      font-weight: bold;
    }
  }
}

.footer-info {
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;

  &__col {
    z-index: 100;
    display: block;
    flex: 1 1 auto;
    max-width: 100%;
    min-width: 15rem;
    margin-block: 1rem;
    &--wide {
      min-width: 20rem;
    }

    &--address {
      position: relative;
      border-radius: 4px;
      padding: 2rem;
      height: 18rem;
      width: 28rem;
      font-size: 2rem;
      @include flexCenter(column);
      gap: 1rem;
    }
    &--offset {
      justify-self: center;
      text-align: center;
      width: 100%;
    }
  }
}

.socialLinks {
  margin-bottom: 1rem;

  &__link {
    margin-left: 2rem;
  }
}
